import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import BoomHome from "./BoomHome/BoomHome";

const useStyles = makeStyles((theme) => ({
  root: {
    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "25ch",
    },
  },
}));

const NewHome = () => {
  const classes = useStyles();

  const [loading, setloading] = useState(true);

  const [alldata, setalldata] = useState([]);

  useEffect(() => {
    fetch("https://evening-journey-57542.herokuapp.com/event")
      .then((res) => res.json())
      .then((data) => {
        setalldata(data);

        setloading(false);
      });
  }, []);
  const [alldata1, setalldata1] = useState("");

  const shuffle = (a) => {
    for (let i = a.length; i; i--) {
      let j = Math.floor(Math.random() * i);
      [a[i - 1], a[j]] = [a[j], a[i - 1]];
    }
  };

  shuffle(alldata);

  return (
    <div style={{ fontFamily: "Besley" }}>
      {loading ? (
        <div className="text-center mt-5 pt-5 bg-white">
          <div className="spinner-border text-danger" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        </div>
      ) : (
        <div>
          <br />
          <h1>
            <span>
              Find Your <span style={{ color: "red" }}>Intern here</span>
            </span>{" "}
          </h1>{" "}
          <br />
          <small>Type Intern Name</small> <br />
          <input
            type="text"
            className="form-control"
            placeholder="searching.."
            onChange={(event) => {
              setalldata1(event.target.value);
            }}
          />
          {alldata
            .filter((v) => {
              if (alldata1 == "") {
                return v;
              } else if (
                v.job_tital.toLowerCase().includes(alldata1.toLowerCase())
              ) {
                return v;
              }
            })
            .map((value) => (
              <BoomHome valuename={value}></BoomHome>
            ))}
        </div>
      )}
    </div>
  );
};

export default NewHome;
