import React, { useState, useEffect } from "react";
import { useParams, useHistory } from "react-router";
import Fakedata from "../../Component/Fakedata/Fakedata.json";
import swal from "sweetalert";

import { useForm } from "react-hook-form";

import "./FormCV.css";

// https://evening-journey-57542.herokuapp.com/

const FormCV = () => {
  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm({
    mode: "onChange",
  });

  const history = useHistory();

  const onSubmit = (data) => {
    // alert(JSON.stringify(data));

    fetch("https://evening-journey-57542.herokuapp.com/addForm", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((data) => {
        swal("Good job!", "Verified Company Added Successfully..!", "success");

        history.replace("/home");
      });
  };

  const { id } = useParams();

  const [newForm, setForm] = useState({});

  useEffect(() => {
    // Fakedata.filter((value) => value.id == id).map((value) => {

    //   setForm(value);

    // });

    fetch(`https://evening-journey-57542.herokuapp.com/event/${id}`)
      .then((response) => response.json())
      .then((data) => setForm(data));
  }, []);

  const [company, setcompany] = useState([]);
  useEffect(() => {
    fetch("https://evening-journey-57542.herokuapp.com/company")
      .then((res) => res.json())
      .then((data) => setcompany(data));
  }, []);

  const user = JSON.parse(localStorage.getItem("user"));

  let isDEVELOPER = company.some((value) => value.email === user.email);

  const [card, setcard] = useState([]);

  useEffect(() => {
    fetch(`https://evening-journey-57542.herokuapp.com/form/${user.email}`)
      .then((response) => response.json())
      .then((data) => setcard(data));
  }, []);

  let isAvailable = card.some((value) => value.companymail == newForm.email);

  //  console.log(isAvailable)

  const backhome = () => {
    history.replace("/home");
  };

  return (
    <div style={{ fontFamily: "Besley" }}>
      {user.emailVerified || user.emailVerified === undefined ? (
        <div>
          <div className="bg-dark text-white">
            <button onClick={backhome}>back homepage</button>
          </div>

          {!isDEVELOPER ? (
            <div className="formcv">
              <h1
                className="p-4"
                style={{ color: "#ec5990", fontSize: "45px" }}
              >
                Information Request Form For Job-Seeker{" "}
              </h1>

              <form onSubmit={handleSubmit(onSubmit)}>
                <label htmlFor="firstName" style={{ color: "#3fe0f2" }}>
                  Full Name
                </label>
                <input
                  placeholder="Bill"
                  {...register("firstName", {
                    required: "this is a required",
                    minLength: {
                      value: 10,
                      message: "Max length is 10",
                    },
                  })}
                />
                {errors.firstName && <p>{errors.firstName.message}</p>}

                <label htmlFor="github" style={{ color: "#3fe0f2" }}>
                  Your (GitHub Or Any hosting place) Project Link{" "}
                </label>
                <input
                  placeholder="github project link"
                  {...register("github", {
                    required: "this is a required",
                  })}
                />
                {errors.github && <p>{errors.github.message}</p>}

                <label htmlFor="link2" style={{ color: "#3fe0f2" }}>
                  Your (GitHub Or Any hosting place) Project Link2{" "}
                </label>
                <input
                  placeholder="github project link"
                  {...register("link2", {
                    required: "this is a required",
                  })}
                />
                {errors.link2 && <p>{errors.link2.message}</p>}

                <label htmlFor="link3" style={{ color: "#3fe0f2" }}>
                  Your (GitHub Or Any hosting place) Project Link3{" "}
                </label>
                <input
                  placeholder="github project link"
                  {...register("link3", {
                    required: "this is a required",
                  })}
                />
                {errors.link3 && <p>{errors.link3.message}</p>}

                <label htmlFor="varsity" style={{ color: "#3fe0f2" }}>
                  {" "}
                  Your University Name :{" "}
                </label>
                <input
                  placeholder="varsity name"
                  {...register("varsity", {
                    required: "this is a required",

                    //

                    minLength: {
                      value: 10,
                      message: "Max length is 10",
                    },

                    //
                  })}
                />
                {errors.github && <p>{errors.github.message}</p>}

                <label htmlFor="email" style={{ color: "#3fe0f2" }}>
                  Email
                </label>
                <input
                  type="text"
                  value={user.email}
                  {...register("email", {
                    required: "this is required",
                    // pattern: {
                    //   value: /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/,
                    //   message: "Invalid email address"
                    // }
                  })}
                />
                {errors.email && <p>{errors.email.message}</p>}

                <label style={{ color: "#3fe0f2" }}>Mobile number</label>
                <input
                  placeholder="+880191"
                  type="tel"
                  {...register("MobileNumber", {
                    pattern: {
                      value: /(^(\+8801|8801|01|008801))[1|3-9]{1}(\d){8}$/,
                      message: "Invalid mobile  number",
                    },
                  })}
                />
                {errors.MobileNumber && <p>{errors.MobileNumber.message}</p>}

                <label htmlFor="companyname" style={{ color: "#3fe0f2" }}>
                  Company Name (Not Change Able)
                </label>
                <input
                  type="text"
                  value={newForm?.companyname}
                  {...register("companyname", {
                    required: true,
                  })}
                />

                <label htmlFor="companymail" style={{ color: "#3fe0f2" }}>
                  Company Gmail (Not Change Able)
                </label>
                <input
                  type="email"
                  value={newForm?.email}
                  {...register("companymail", {
                    required: true,
                  })}
                />

                <label htmlFor="dateValue" style={{ color: "#3fe0f2" }}>
                  Current date-time (Not Change Able)
                </label>
                <input
                  type="dateValue"
                  value={new Date().toLocaleString()}
                  {...register("dateValue", {
                    required: true,
                  })}
                />

                <label>Title</label>
                <select {...register("Title")}>
                  <option value="Male">Male</option>
                  <option value="Female">Female</option>

                  <option value="Another">Another</option>
                </select>

                {/* {formdata1 && companygmail ?null  : <input type="submit" />} */}

                {!isAvailable && <input type="submit" />}

                <div style={{ height: "200px" }}></div>
              </form>
            </div>
          ) : (
            <div>
              <h1>We have no information about you ...... </h1>
            </div>
          )}
        </div>
      ) : (
        <div className="text-center">
          <h1>logged out and verified your email..</h1>
          <div className=" text-white">
            <button onClick={backhome}>back homepage</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default FormCV;
