import React, { useState, useEffect } from "react";

const Showcv = () => {
  const [student1, setStudent1] = useState([]);

  let user = JSON.parse(localStorage.getItem("user"));

  useEffect(() => {
    fetch(
      `https://evening-journey-57542.herokuapp.com/companyForm/${user.email}`
    )
      .then((response) => response.json())
      .then((data) => setStudent1(data));
  }, []);
  console.log(student1);

  return (
    <div className="row">
      <h2 className="text-center" style={{ fontFamily: "Besley" }}>
        {" "}
        Intern CV Details
      </h2>

      {student1.map((card) => (
        <div
          style={{ fontFamily: "Besley" }}
          className="text-center col-lg-4 pt-lg-5 ps-lg-5 ms-lg-5 pb-lg-5  mt-lg-5 border col-md-6 pt-md-5 bg-dark text-white col-sm-12 rounded"
        >
          <h5>Name : {card.firstName}</h5>
          <hr />

          <p>Email : {card.email}</p>

          <h6>Number : {card.MobileNumber}</h6>

          <h5>Github link </h5>
          <p>{card.github}</p>
          <p>{card.link2}</p>
          <p>{card.link3}</p>

          <h6>{card?.varsity}</h6>
          <p>Submit date : {card?.dateValue}</p>
          <h6>Title : {card.Title}</h6>
        </div>
      ))}
    </div>
  );
};

export default Showcv;
