import React from "react";

import Modal from "react-modal";
import { useForm } from "react-hook-form";

const customStyles = {
  content: {
   
    left: "30%",
    right:"30%",
    // right: "100px",
    // bottom: "auto",
   
    
    marginRight: "",
   
  },
};

Modal.setAppElement("#root");

function ModalPage({
  openModal,
  closeModal,
  modalIsOpen,
  onSubmit,
  puredata2,

}) {

  console.log(puredata2)

  




  const { register, handleSubmit } = useForm();
  let subtitle;

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    subtitle.style.color = "#f00";
  }

  return (
    <div>
      {/* <button className="btn btn-primary" >
        Open Modal
      </button> */}
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <h2 ref={(_subtitle) => (subtitle = _subtitle)}></h2>
        
       
       

        <div className="row">
          <div className="col-3"> <button className="btn btn-dark" onClick={closeModal}>
          close
        </button></div>
          <div className="col-9"> <h6>Confirm Intern Candidate to interview</h6>
</div>
        </div>
        
        <div>
          <div className="mehedi">
            <form onSubmit={handleSubmit(onSubmit)}>
              <div>
                <label htmlFor="datetime">Date for Interview</label>
                <input
                  required
                  {...register("datetime")}
                  type="datetime-local"
                />
              </div>

              <div>
                <label htmlFor="place">Place of Interview</label>
                <input required placeholder="place" {...register("place")} />
              </div>
              <div>
              <label htmlFor="place">Company email</label>
                
                <input
                  value={puredata2.companymail}
                  placeholder="companyMail"
                  {...register("companyMail", {
                    required: true,
                  })}
                />
              </div>
              <div>
                <label htmlFor="place">Company Name</label>
              
                <input
                  value={puredata2.companyname}
                  placeholder="companyname"
                  {...register("companyname", {
                    required: true,
                  })}
                />
              </div>
              <div>

                <label htmlFor="place">Intern email</label>
               
                <input
                  value={puredata2.email}
                  placeholder="place"
                  {...register("email", {
                    required: true,
                  })}
                />
              </div>

              <input value="CONFIRM" type="submit" />
            </form>
          </div>
        </div>
      </Modal>
    </div>
  );
}

export default ModalPage;
