import React, { useState, useEffect } from "react";
import { useHistory } from "react-router";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import swal from "sweetalert";

const CompanyDelete = () => {
  const history = useHistory();

  const [deleteitem, setDelete] = useState([]);

  useEffect(() => {
    fetch(`https://evening-journey-57542.herokuapp.com/companyTotal`)
      .then((response) => response.json())
      .then((data) => setDelete(data));
  }, []);

  const handledelete = (id) => {
    fetch(`https://evening-journey-57542.herokuapp.com/deleteCompany/${id}`, {
      method: "DELETE",
    })
      .then((response) => response.json())
      .then((data) => {
        swal({
          title: "Good job!",
          text: "Bad Company Deleted Successfully!",
          icon: "success",
          button: "Aww yiss!",
        });
        history.replace("/admin");
      });
  };

  const [alldata1, setalldata1] = useState("");

  return (
    <div style={{ fontFamily: "Besley" }}>
      <h4 className="text-center pt-2">Delete Company</h4>
      <br />
      <small>No of company : {deleteitem.length}</small> <br />
      <input
        type="text"
        className="form-control shadow-sm"
        placeholder="only email please.."
        onChange={(event) => {
          setalldata1(event.target.value);
        }}
      />
      <br />
      <div className="shadow pt-3">
        <div className="row ">
          <div
            style={{
              backgroundColor: "rgba(255, 0, 0, 0.438)",
              color: "white",
              fontWeight: "bold",
            }}
            className=" text-center col-3  p-4 ms-5 border rounded shadow "
          >
            Company Name
          </div>
          <div
            style={{
              backgroundColor: "rgba(228, 164, 47, 0.541)",
              color: "#786c56",
              fontWeight: "bold",
            }}
            className="text-center col-3  p-4 ms-5 border rounded shadow"
          >
            Company Email
          </div>
          <div
            style={{
              backgroundColor: "rgba(71, 160, 60, 0.411)",
              color: "white",
              fontWeight: "bold",
            }}
            className="text-center col-3  p-4 ms-5 border rounded shadow"
          >
            Delete
          </div>
          <div className="offset-1"></div>
        </div>

        {deleteitem
          .filter((v) => {
            if (alldata1 == "") {
              return v;
            } else if (v.email.includes(alldata1)) {
              return v;
            }
          })
          .map((value) => (
            <div className="p-4">
              <div className="row shadow-sm p-2">
                <div className="col-4 ms-5">
                  <h4>{value.name}</h4>
                </div>
                <div className="col-3 text-center">
                  {" "}
                  <p>{value.email} </p>
                </div>
                {console.log("v" + value)}

                <div className="col-3 text-center">
                  {" "}
                  <button
                    className="btn "
                    onClick={() => handledelete(value._id)}
                  >
                    <DeleteForeverIcon />
                  </button>
                </div>
              </div>
            </div>
          ))}
      </div>
    </div>
  );
};

export default CompanyDelete;
