import React, { useState, useEffect } from "react";
import { useHistory } from "react-router";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import swal from "sweetalert";
import { BoxesLoader } from "react-awesome-loaders";

const Delete = () => {
  const history = useHistory();

  const [deleteitem, setDelete] = useState([]);
  const [loading, setloading] = useState(true);

  useEffect(() => {
    fetch(`https://evening-journey-57542.herokuapp.com/event`)
      .then((response) => response.json())
      .then((data) => {
        setDelete(data);

        setloading(false);
      });
  }, []);

  const handledelete = (id) => {
    fetch(`https://evening-journey-57542.herokuapp.com/deleteInfo/${id}`, {
      method: "DELETE",
    })
      .then((response) => response.json())
      .then((data) => {
        swal("Good job!", "Deleted Intern Circular successfully!", "success");
        history.replace("/admin");
      });
  };

  return (
    <div style={{ fontFamily: "Besley" }}>
      {loading ? (
        <div
          style={{ marginTop: "140px" }}
          className="d-flex justify-content-center"
        >
          <BoxesLoader
            boxColor={"#345254"}
            style={{ marginBottom: "20px" }}
            desktopSize={"98px"}
            mobileSize={"60px"}
          />
        </div>
      ) : (
        <div>
          <h3 className="text-center p-4">Delete Intern Circular</h3>
          <br />

          <div className="shadow ps-5 pt-4">
            <div className="row ">
              <div
                style={{ backgroundColor: "rgba(0, 255, 234, 0.404)" }}
                className="col-3 p-3 ms-2 border rounded shadow "
              >
                <h6
                  className="text-center"
                  style={{ color: "white", fontWeight: "bold" }}
                >
                  Company Name
                </h6>
              </div>

              <div
                style={{ backgroundColor: "rgba(255, 0, 0, 0.438)" }}
                className="col-3 p-3 ms-3 border rounded shadow"
              >
                <h6
                  className="text-center"
                  style={{ color: "white", fontWeight: "bold" }}
                >
                  Company Email
                </h6>
              </div>

              <div
                style={{ backgroundColor: "rgba(204, 34, 226, 0.555)" }}
                className="col-3 p-4 ms-4 border rounded shadow"
              >
                <h6
                  className="text-center"
                  style={{ color: "#786c56", fontWeight: "bold" }}
                >
                  Location
                </h6>
              </div>

              <div
                style={{ backgroundColor: "rgba(228, 164, 47, 0.541)" }}
                className="col-1 p-3 ms-4 border rounded shadow"
              >
                <h6
                  className="text-center"
                  style={{ color: "white", fontWeight: "bold" }}
                >
                  Delete
                </h6>
              </div>
            </div>

            {deleteitem.map((value) => (
              <div className="p-4">
                <div className="row shadow-sm" style={{ color: "#496669" }}>
                  <div className="col-4">
                    <h4>{value.companyname}</h4>
                  </div>
                  <div className="col-3">
                    {" "}
                    <p>{value.email} </p>
                  </div>

                  <div className="col-3">
                    {" "}
                    <p> Location : {value.city} </p>
                  </div>
                  <div className="col-2">
                    {" "}
                    <button
                      className="btn "
                      onClick={() => handledelete(value._id)}
                    >
                      <DeleteForeverIcon style={{ color: "red" }} />
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default Delete;
