import React, { useState, useEffect, useRef } from "react";

import { useParams, useHistory } from "react-router";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrashAlt } from "@fortawesome/free-solid-svg-icons";
import { faCheck } from "@fortawesome/free-solid-svg-icons";
import { faThumbsUp } from "@fortawesome/free-solid-svg-icons";

import ReactDOM from "react-dom";
import "./index10.css";
import { useForm } from "react-hook-form";
import ModalPage from "./ModalPage";

const ApproveCV = () => {
  const intervalRef = useRef();

  const history = useHistory();
  const [student, setStudent] = useState([]);

  const { register, handleSubmit } = useForm();

  const [puredata2, setPuredata2] = useState({});
  console.log(puredata2);

  const onSubmit = (data) => {
    fetch("https://evening-journey-57542.herokuapp.com/confirm", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((data) => {});

    settik(true);

    fetch(
      `https://evening-journey-57542.herokuapp.com/deleteCFcv/${puredata2._id}`,
      {
        method: "DELETE",
      }
    )
      .then((response) => response.json())
      .then((data) => {
        alert("Thank You for Cofirm Interview.");
        history.replace("/company/nayeem");
      });
  };

  const [toggled, settoggled] = useState(false);

  const [tik, settik] = useState(false);

  let user = JSON.parse(localStorage.getItem("user"));

  useEffect(() => {
    fetch(
      `https://evening-journey-57542.herokuapp.com/companyForm/${user.email}`
    )
      .then((response) => response.json())
      .then((data) => setStudent(data));
  }, []);

  const handledelete = (id) => {
    const value = student.filter((v) => v._id == id);

    setPuredata2(value[0]);

    settoggled(!toggled);
  };

  const [alldata1, setalldata1] = useState("");

  //modal dialog

  const [modalIsOpen, setIsOpen] = React.useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <div className="">
      <br /> <br />
      <h3 style={{ fontWeight: "bold" }}>
        <span style={{ color: "#57368a" }}>APPROVE Cv</span> if jobseekers
        deserve it...
      </h3>
      <small>If u want to search please give email id</small>
      <input
        type="text"
        className="form-control"
        placeholder="email id searching.."
        onChange={(event) => {
          setalldata1(event.target.value);
        }}
      />
      <div className="row">
        <div className="">
          {student
            .filter((v) => {
              if (alldata1 == "") {
                return v;
              } else if (
                v.email.toLowerCase().includes(alldata1.toLowerCase())
              ) {
                return v;
              }
            })
            .map((value) => (
              <div>
                <br /> <hr />
                <div className="row">
                  <div className="col-3 ">
                    <h5
                      className="text-center"
                      style={{ color: "#786c56", fontWeight: "bold" }}
                    >
                      {value.firstName}
                    </h5>
                  </div>

                  <div className="col-4">
                    <h6 style={{ color: "#0c6f99", fontWeight: "bold" }}>
                      {value.email}
                    </h6>
                  </div>

                  <div className="col-2 ">
                    <button
                      className="btn btn-primary"
                      onClick={() => [handledelete(value._id), openModal()]}
                    >
                      Confirm to Interview
                    </button>
                  </div>
                </div>
              </div>
            ))}
        </div>

        <ModalPage
          modalIsOpen={modalIsOpen}
          closeModal={closeModal}
          onSubmit={onSubmit}
          puredata2={puredata2}
        />
      </div>
    </div>
  );
};

export default ApproveCV;
